/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .mkdf-widget-title-holder {
        margin-bottom: $widget-title-padding;

        .mkdf-widget-title {
            color: $widget-heading-color;
            margin-bottom: 0;
        }

        .mkdf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;
            text-align: center;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            border-top: 1px solid $widget-list-item-border-color;
            margin-top: $widget-list-item-padding;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .mkdf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    @include mkdfTransition(all .2s ease);

                    &:after {
                        content: '';
                        height: 1px;
                        width: 100%;
                        position: absolute;
                        left: 0;
                        bottom: 2px;
                        background-color: $widget-link-color;
                        opacity: 0;
                        @include mkdfTransition(all .2s ease);
                    }

                    &:hover {
                        &:after {
                            opacity: 1;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .mkdf-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rsswidget {
            color: #777;

            &:hover {
                color: $default-text-color;
            }
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }

        ul {
            li {
                padding: 15px 0;
            }
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .mkdf-searchform {

        .mkdf-search-label {
            font-size: 12px;
            font-family: $default-heading-font;
            font-style: italic;
            letter-spacing: 0.3em;
            float: left;
        }

        .input-holder {
            border-bottom: 1px solid $default-border-color;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                font-family: $default-heading-font;
                font-size: 12px;
                font-weight: inherit;
                line-height: 29px;
                outline: 0;
                position: relative;
                vertical-align: middle;
                -webkit-appearance: none;
                @include mkdfTransition(color .2s ease-in-out);
                letter-spacing: 0.05em;
                font-style: italic;
                text-transform: uppercase;
            }

            input.search-field {
                width: calc(100% - 40px);
                color: $default-heading-color;
                letter-spacing: 0.3em;

                @include mkdfPlaceholder {
                    color: inherit;
                }
            }

            button {
                color: $widget-text-color;
                cursor: pointer;
                font-size: 16px;
                width: 34px;
                height: 38px;
                padding: 0;
                line-height: 38px;
                @include mkdfTransition(color .2s ease-in-out);

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    &.widget_archive,
    &.widget_categories,
    &.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include mkdfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        a {
            border-radius: 2px;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 11px !important;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            margin: 0 7px 7px 0;
            padding: 0 19px;
            text-transform: uppercase;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}