@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 71px 0 20px;
    }

    .mkdf-comment {
        display: table;

        &.mkdf-pingback-comment {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 10px 0 0;
            padding: 0;
            background-color: #f8fbf6;

            &:nth-child(2n) {
                background-color: #fff;
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 35px;

        > * {
            margin: 0;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        display: table-cell;
        float: left;
        padding: 41px 0 0 25px;
        vertical-align: middle;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .mkdf-comment-text {
        padding: 41px 52px 38px;
        min-height: 82px;
        display: table-cell;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 6px 0 9px;
            width: auto;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin: 7px 0 0 15px;
            font-size: 12px;
            color: $default-heading-color;

            &:hover {
                color: $first-main-color;
            }
        }

        .comment-reply-link {
            @include mkdfTransform(rotateX(360deg) rotateY(180deg));

            &:before {
                content: "\f3e5";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            float: right;
            margin: 11px 0 0;
            @include mkdfP();
        }

        .mkdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 60px 0;

            textarea,
            input[type='text'] {
                border-bottom: 1px solid #ddd !important;
            }

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .mkdf-comment-form-inner {
        padding: 47px 0 80px;

        .comment-form {
            background-color: #fef8f7;
            padding: 40px 40px 130px;

            .comment-massage-label,
            .comment-name-label,
            .comment-email-label {
                display: block;
                text-align: center;
                padding-bottom: 3px;
                @include mkdfP();
            }

            > div {
                width: 47%;
                float: left;
                text-align: center;
            }

            > div + div {
                float: right;
            }

            .comment-name-label,
            .comment-email-label {
                display: inline-block;
                vertical-align: top;
            }
        }
    }

    .comment-reply-title {
        margin: 0 0 35px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .mkdf-comment-date {
                margin-top: 4px;
            }

            #cancel-comment-reply-link {
                margin-left: 0;
            }

            .comment-respond {
                .form-submit {
                    text-align: center;

                    #submit_comment {
                        float: none;
                        right: 0;
                    }

                }
            }
        }
    }

    .mkdf-comment-form {
        .mkdf-comment-form-inner {
            .comment-form {
                padding: 40px 25px 100px;

                > div {
                    width: 100%;

                    .form-submit {
                        text-align: center;

                        #submit_comment {
                            float: none;
                            right: 0;
                        }

                    }
                }
            }
        }

        .form-submit {
            text-align: center;

            #submit_comment {
                float: none;
                right: 0;
            }
        }
    }


}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment {
            display: block;

            .mkdf-comment-image {
                display: none;
            }

            .mkdf-comment-text {
                display: block;
                padding: 20px 35px;
                min-height: inherit;
            }
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize('medium');
    cursor: pointer;
    background-color: transparent;
    border: 0;
    padding: 11px 13px;

    &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 1px;
        background-color: $default-border-color;
    }

    &:hover {
        background-color: transparent;
        border: 0;
    }
}

#submit_comment{
    float: right;
    border: none;
    background-color: transparent;
    color: #000;
    border-top: 1px solid $default-border-color;
    font-size: 12px;
    padding: 0 35px;
    margin-top: 38px;

    &:hover {
        background-color: transparent;
        color: inherit;
        border-top: 1px solid $default-border-color;
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
    background-color: #fff;
    border: none;
    min-height: 172px;
    max-width: 100%;
    margin-bottom: 36px;
    padding-top: 15px;
}

#respond input[type="text"] {
    display: inline-block;
    background-color: #fff;
    font-size: 13px;
    border: none;
    float: left;

    &:last-of-type {
        float: right;
    }
}

.comment-form-cookies-consent {
    clear: both;
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.mkdf-cf7-rf {
    background-color: rgba(250,245,240, .6);
    padding: 90px 51px;
    box-sizing: border-box;
    text-align: center;

    label,
    span {
        font-size: 13px;
    }

    input[type="text"],
    .wpcf7-form-control.wpcf7-textarea,
    input[type="email"] {
        background-color: #fff;
        border: none;
        top: -2px;
        margin-bottom: 34px;
    }

    .wpcf7-checkbox {
        display: block;
        text-align: right;

        .wpcf7-list-item-label {
            float: left;
        }

        input[type="checkbox"] {
            vertical-align: middle;
        }
    }

    .mkdf-cf7-rf-date{
        >label {
            display: inline-block;
            width: 30%;

            &:first-of-type{
                float: left;
            }

            &:last-of-type{
                float: right;
            }
        }
    }

    .mkdf-cf7-rf-guests {
        min-height: 95px;

        >label {
            display: inline-block;
            width: 30%;
            float: left;

            &:nth-child(2) {
                margin-left: 25px;
            }
        }
    }

    .wpcf7-radio.guests {
        margin-top: 30px;
        display: inline-block;
        float: right;

        .wpcf7-list-item {
            margin: 0;
        }

        .wpcf7-list-item-label {
            position: relative;
            top: -2px;
        }
    }

    .mkdf-cf7-rf-2-col {
        >label {
            width: 45%;

            &:first-of-type {
                float: left;
            }

            &:nth-of-type(2) {
                float: right;
            }
        }
    }

    .mkdf-cf7-rf-msg {
        &:before {
            display: block;
            clear: both;
            content: '';
        }

        .wpcf7-form-control.wpcf7-textarea {
            top: 2px;
        }
    }

    .mkdf-cf7-rf-btn {
        text-align: right;
        padding-top: 16px;

        .mkdf-cf7-rf-btn-inner {
            display: inline-block;
            width: auto;
            position: relative;

            .wpcf7-form-control.wpcf7-submit {
                padding: 1px 13px;
            }

            &:before {
                height: 1px;
                width: 100%;
                background-color: $default-border-color;
                position: absolute;
                top: 1px;
                left: 0;
                content: '';
            }

            .ajax-loader {
                display: none;
            }
        }
    }
}

.ui-timepicker-div {
    [data-icon]:before {
        content: '';
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include mkdfTransform(translateY($pos));
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        @include phone-landscape {
            display: none;
        }

        .owl-prev,
        .owl-next {
            padding: 30px;
            box-sizing: content-box;
            width: 116px;
            position: absolute;
            top: 50%;
            margin: 0;
            color: $first-main-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            @include mkdfTransform(translateY(-50%));
            @include mkdfTransition(color .2s ease-out, opacity .4s);

            &.disabled {
                opacity: 0;
                pointer-events: none;
            }

            &:hover {
                color: $first-main-color;           
                
                span:before {
                    width: 80%;
                }
            }

            > span {
                position: relative;
                display: block;
                padding: 10px;
                border: none;
                overflow: hidden;
                line-height: 1em;
                color: inherit;

                &:before {
                    content: "";
                    position: absolute;
                    border-collapse: collapse;
                    border-top: 1px solid currentColor;
                    width: 100%;
                    height: 1px;
                    @include mkdfTransition(.4s);
                }
            }
        }

        .owl-prev {
            left: -135px;

            span {

                &:before {
                    left: 0;
                }
            }
        }

        .owl-next {
            right: -135px;

            span {

                &:before {
                    right: 0;
                }
            }
        }

        .mkdf-nav-outer & {

            @include laptop-landscape-mac {
                .owl-prev {
                    width: 87px;
                    left: -80px;
                }
        
                .owl-next {
                    width: 87px;
                    right: -80px;
                }
            }

            @include ipad-landscape {

                .owl-prev {
                    left: -90px;
                }
        
                .owl-next {
                    right: -90px;
                }
            }
        }

        .mkdf-nav-inner & {

            .owl-prev {
                left: -30px;

                &:hover {

                    &:before {
                        right: 25px;
                    }
                }

                &:before {
                    right: 0;
                    left: initial !important;
                    @include mkdfTransition(.4s);
                }
            }

            .owl-next {
                right: -30px;

                &:hover {

                    &:before {
                        left: 25px;
                    }
                }

                &:before {
                    left: 0;
                    right: initial !important;
                    @include mkdfTransition(.4s);
                }
            }
        }

        .mkdf-navigation-numbers & {

            .owl-prev, .owl-next {

                &:before {
                    position: absolute;
                    color: $default-text-color;
                    min-width: 12px;
                    top: -20px;
                    font-size: 15px;
                    padding: 30px;
                    font-family: $additional-font;
                }
            }

            .owl-prev {

                &:before {
                    content: attr(data-prev-num);
                    left: 0;
                }
            }

            .owl-next {

                &:before {
                    content: attr(data-next-num);
                    right: 0;
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 30px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 1px solid rgba($default-heading-color,.4);
                box-sizing: border-box;

                @include mkdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include mkdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        @include mkdfTableLayout();
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .mkdf-404-title {
            margin: 0;
        }

        .mkdf-404-subtitle {
            margin: 35px 0 0;
        }

        .mkdf-404-text {
            margin: 10px 0 0;
        }

        .mkdf-btn {
            margin: 30px 0 0;

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .mkdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .mkdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 51px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        @include mkdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include mkdfTransition(opacity 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 49px;
        height: 49px;
        line-height: 50px;
        font-size: 14px;
        text-align: center;
        font-family: $additional-font;
        font-weight: 400;
        text-transform: uppercase;
        color: $default-heading-color;
        background-color: #faf5f0;
        border: 1px solid #faf5f0;
        border-radius: 50%;

        @include mkdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);


        &:before {
            content: '';
            position: absolute;
            left: calc(50% - 1px);
            top: -16px;
            width: 1px;
            height: 35px;
            background-color: $first-main-color;
            @include mkdfTransform(rotate(0deg));
            transform-origin: 0 100%;
            @include mkdfTransition(transform .5s ease);
        }

        &:hover {
            &:before {
                @include mkdfTransform(rotate(-120deg));
                transform-origin: 100% 100%;
            }
        }
    }

    .mkdf-back-to-top-text {
        margin-right: -0.05em;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
    .mkdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   #Date Picker Start
   ========================================================================== */

.ui-datepicker {
    display: none;

    button {
        cursor: pointer;
    }

    button[type="button"] {
        background-color: transparent;
        border: none;
        font-weight: 400;
        font-family: $additional-font;
        letter-spacing: .2em;
        text-transform: uppercase;
        position: relative;
        margin: 0 10px 10px 0;

        &:before {
            content: '';
            position: absolute;
            top: -3px;
            left: 0;
            height: 1px;
            width: 100%;
            background-color: $default-border-color;
        }

        &:hover {
            background-color: transparent;
        }
    }
}

/* ==========================================================================
   #Date Picker End
   ========================================================================== */