aside.mkdf-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 55px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;
    $widget-title-padding: 49px;

    // widget color
    $widget-background-color: #eeeeee;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 5px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: inherit;

    // links
    $widget-link-color: $default-text-color;
    $widget-link-hover-color: $default-text-color;

    // specific widgets
    $widget-search-form-height: 50px;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';
}

aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        .mkdf-widget-title-holder {
            text-align: center;

            .mkdf-widget-title {
                position: relative;

                &:after {
                    content: '';
                    height: 20px;
                    width: 1px;
                    background-color: $default-border-color;
                    position: absolute;
                    bottom: -33px;
                    left: 50%;
                    @include mkdfTransform(translateX(-50%));
                }
            }
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

}